@media screen and (min-width: 576px) {
  // Burger btn
  .burger-btn {
    top: 40px;
    right: 40px;
  }
  // Navbar
  .navbar {
    width: 50vw;
  }
  // Hero
  .hero {
    padding: 15em 2em;

    &__title {
      font-size: 4.8rem;
    }
    &__text {
      font-size: 2.6rem;
    }
  }
  // Islands
  .islands {
    &__card-img-title {
      font-size: 3.6rem;
    }
  }
  // Stats
  .statistics {
    &__boxes {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
    }
    &__box {
      width: 50%;
      padding: 2em 1em;

      &:nth-child(2) {
        padding-top: 0;
      }

      &:nth-child(5) {
        padding-bottom: 0;
      }
    }
  }
}